export default {
	data() {
		return {
			showVideo: true,
			labelIndex: 0,
			ratingsList: [],
			labelList: [
				{
					name: '全部',
					number: 50,
					type: 'all'
				},
				{
					name: '好评',
					number: 25,
					type: 'good'
				},
				{
					name: '中评',
					number: 8,
					type: 'secondary'
				},
				{
					name: '差评',
					number: 1,
					type: 'poor'
				},
				{
					name: '有图',
					number: 12,
					type: 'img'
				},
				{
					name: '视频',
					number: 2,
					type: 'video'
				},
				{
					name: '追加',
					number: 2,
					type: 'append'
				}
			],
			videoSrc: '',
			videoContext: null,
			animationDataArr: [],
			animationData: {}
		};
	},
	onLoad() {
		try {
			const comments = uni.getStorageSync('comments');
			if (comments) {
				this.ratingsList = comments;
			}
		} catch (e) {
			console.log(e);
		}
		this.showVideo = false;
		

		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation();

		
	},
	onUnload() {
		// 页面卸载的时候，清除动画数据
		this.animationData = {};
		this.animationDataArr = [];
	},
	onReady() {
		this.videoContext = uni.createVideoContext('myVideo');
	},
	methods: {
		loadRatings(index) {
			this.labelIndex = index;
		},
		showBigImg(image, images) {
			uni.previewImage({
				current: image,
				urls: images
			});
		},
		playVideo(path) {
			this.videoSrc = path;

			//全屏播放
			this.$nextTick(function() {
				this.videoContext.requestFullScreen({
					direction: 0
				});
			});
		},
		videoControl(e) {
			if (e.detail.fullScreen) {
				// 全屏
				// 自动播放
				this.videoContext.play();
			} else {
				// 停止播放
				this.videoContext.stop();
				this.videoPause();
			}
		},
		videoPause() {
			this.videoSrc = '';

			this.showVideo = false;

		},
		// 实现点赞动画效果
		praiseMe(e) {

			var gIndex = e.currentTarget.dataset.gindex;
			console.log(gIndex);
			// return;
			// 构建动画数据，并且通过step来表示这组动画的完成
			this.animation
				.translateY(-60)
				.opacity(1)
				.step({
					duration: 400
				});

			// 导出动画数据到view组件，实现组件的动画效果
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[gIndex] = this.animationData.export();

			// 还原动画
			setTimeout(
				function() {
					this.animation
						.translateY(0)
						.opacity(0)
						.step({
							duration: 0
						});
					this.animationData = this.animation;
					this.animationDataArr[gIndex] = this.animationData.export();
				}.bind(this),
				500
			);

		}
	}
};
